.weather-box {
  text-align: center;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 50, 0.5);
}
.temp {
  background-color: rgba(255, 255, 255, 0.5);
  position: relative;
  display: inline-block;
  padding: 1rem 1.6rem;
  border-radius: 1rem;
  margin: 1rem;
  font-size: 5rem;
  box-shadow: 3px 4px rgba(50, 50, 50, 0.5);
}
.weather {
  font-size: 2rem;
}
